import React from 'react'
import { useState, useEffect } from 'react'
import { NavLink } from 'react-router-dom'
import '../CarteLocation/CarteLocation.css'

export default function CarteLocation() {
  const [jsonData, setjsonData] = useState([])

  useEffect(() => {
    const getHome = async () => {
      const response = await fetch(
        'https://raw.githubusercontent.com/Ines-1958/projet11/b9e3840e17dda0262e8b70ec00dac8a03ca562d8/public/donnees-json/logements.json'
      )

      if (!response.ok) {
        const message = `An error has occured: ${response.status}`
        throw new Error(message)
      }

      const json = await response.json()
      setjsonData(json)
    }
    getHome()
  }, [])

  return (
    <div className="container-location">
      <div className="content-location">
        {jsonData &&
          jsonData.map((home, index) => (
            <NavLink
              key={index}
              className="carte-location"
              to={`/logement/${home.id}`}
            >
              <div
                className="location"
                key={`${'home'}-${index}`}
                id={`${'home'}-${index}`}
              >
                {home.title}
              </div>
            </NavLink>
          ))}
      </div>
    </div>
  )
}
